*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

nav {
  ul {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: minmax(2em, 10vh);
    background-color: #333;
    justify-items: center;
    align-items: center;
    list-style: none;
    a {
      text-decoration: none;
      color: white;
      font-size: 1em;
      font-family: sans-serif;
      font-weight: bold;
      text-shadow: 0px 0px 2px white;
    }
  }
  
}

main {
  display: grid;
  grid-template-columns: 3fr 1fr;
  grid-template-rows: 4fr 1fr;
  justify-content: center;

  .big_img {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    display: flex;
    background-color: black;
    margin: 2% 0;
    img {
      width: 80%;
      margin: auto;
    }
  }

  .thumb_img {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    display: grid;
    grid-template-columns: 1fr 2fr 2fr 2fr 2fr 2fr 1fr;
    grid-auto-flow: row;
    background-color: #444;
    align-content: center;
    justify-content: center;
    grid-gap: .5%;

    .fas {
      display: flex;
      width: 100%;
      margin: auto;
      justify-content: center;
      font-size: 5vw;

      .fa-angle-left {
        grid-column: 1 / 2;
      }
      
      .fa-angle-right {
        grid-column: 7 / 8;
      }
    }

    .slot01 {
      grid-column: 2 / 3;
    }

    .slot05 {
      grid-column: 6 / 7;
    }
    
    img {
      display:flex;
      margin: auto;
      width: 100%;
    }
  }
}